import * as React from "react";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import SendIcon from "@material-ui/icons/Send";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/HighlightOff";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import * as marathiReading from './../data/mar-read.json';
import * as marathiWriting from './../data/mar-write.json';
import * as englishReading from './../data/eng-read.json';
import * as englishWriting from './../data/eng-write.json';
import intl from 'react-intl-universal';
import { marathiInteractiveChoices, englishInteractiveChoices } from "./Constants";
import CardActionArea from '@material-ui/core/CardActionArea';

import {
    marathiChoices,
    englishChoices
} from "./Constants";

const useStyles = makeStyles((theme) => ({
    button: (props) => ({
        margin: theme.spacing(1),
        backgroundColor: props.color,
        color: "#fff",
        '&:hover': {
            backgroundColor: props.color,
            color: '#FFF'
        }
    }),
    card: {
        color: theme.palette.primary.contrastText,
        textAlignLast: "center",
        borderRadius: 0,
    },
    media: {
        height: 0,
        paddingTop: "56.25%",
    },
    cardActions: {
        justifyContent: "center",
    },
    closeIcon: {
        cursor: "pointer",
        float: "right",
        padding: '0px'
    },
    dialogColor: {
        backgroundColor: "#fff9f5",
    },
}));

const DashboardItem = (props: any) => {

    const classes = useStyles(props.item);
    const [open, setOpen] = React.useState(false);
    const [contentLanguageId, setContentLanguage] = React.useState(0);

    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

    let setChoices: any = [];

    if (props.item.id === 1) {
        setChoices = marathiChoices;
    }
    if (props.item.id === 2) {
        setChoices = englishChoices;
    }

    const handleClickOpen = (selectedLanguageId: number) => {
        setContentLanguage(selectedLanguageId);
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const showContentItems = (contentPrefrenceId: number) => {
        //hide dashboard view and load selected content index
        setOpen(false);

        if ((contentLanguageId === 1 && contentPrefrenceId === 3) ||
            (contentLanguageId === 2 && contentPrefrenceId === 3) ||
            (contentLanguageId === 3 && contentPrefrenceId === 3)) {
            props.handleDashboardView(true, getLanguageContent(contentLanguageId, contentPrefrenceId),
                getLanguageHeader(contentLanguageId, contentPrefrenceId));
        } else {
            props.handleDashboardView(false, getLanguageContent(contentLanguageId, contentPrefrenceId),
                getLanguageHeader(contentLanguageId, contentPrefrenceId));
        }

    };

    const getLanguageContent = (languageId: number, preferenceId: number) => {
        if (languageId === 1 && preferenceId === 1) {
            return marathiReading.data;
        }
        if (languageId === 1 && preferenceId === 2) {
            return marathiWriting.data;
        }
        if (languageId === 1 && preferenceId === 3) {
            return marathiInteractiveChoices;
        }

        if (languageId === 2 && preferenceId === 1) {
            return englishReading.data;
        }
        if (languageId === 2 && preferenceId === 2) {
            return englishWriting.data;
        }
        if (languageId === 2 && preferenceId === 3) {
            return englishInteractiveChoices;
        }

        return null;
    }

    const getLanguageHeader = (languageId: number, preferenceId: number) => {
        if (languageId === 1 && preferenceId === 1) {
            return intl.get('LANGUAGE_MARATHI') + " " + intl.get('LANGUAGE_READ_TITLE');
        }
        if (languageId === 1 && preferenceId === 2) {
            return intl.get('LANGUAGE_MARATHI') + " " + intl.get('LANGUAGE_WRITE_TITLE');
        }
        if (languageId === 1 && preferenceId === 3) {
            return intl.get('LANGUAGE_MARATHI') + " " + intl.get('LANGUAGE_SPEAK_TITLE');
        }
        if (languageId === 2 && preferenceId === 1) {
            return intl.get('LANGUAGE_ENGLISH') + " " + intl.get('LANGUAGE_READ_TITLE');
        }
        if (languageId === 2 && preferenceId === 2) {
            return intl.get('LANGUAGE_ENGLISH') + " " + intl.get('LANGUAGE_WRITE_TITLE');
        }
        if (languageId === 2 && preferenceId === 3) {
            return intl.get('LANGUAGE_ENGLISH') + " " + intl.get('LANGUAGE_SPEAK_TITLE');
        }
        return intl.get('APP_NAME');
    }

    return (
        <div>
            {props.item ? (
                <div>

                    <Card className={classes.card}>
                        <CardActionArea onClick={() => handleClickOpen(props.item.id)}>
                            <CardMedia
                                className={classes.media}
                                image={props.item.url}
                            />
                        </CardActionArea>
                        <CardActions className={classes.cardActions}>
                            <Button
                                variant="contained"
                                size="large"
                                className={classes.button}
                                endIcon={<SendIcon />}
                                onClick={() => handleClickOpen(props.item.id)}
                            >
                                <Typography variant="h5" color="inherit">
                                    {intl.get(props.item.title)}
                                </Typography>
                            </Button>
                        </CardActions>
                    </Card>

                    <Dialog
                        disableBackdropClick
                        disableEscapeKeyDown
                        fullScreen={fullScreen}
                        fullWidth={true}
                        maxWidth={'lg'}
                        open={open}
                        onClose={handleClose}

                    >
                        <DialogTitle id="responsive-dialog-title" className={classes.dialogColor}>
                           
                            {intl.get('SELECT_PREFERENCE')}
                            <IconButton
                                className={classes.closeIcon}
                                color="primary"
                                onClick={handleClose}
                                aria-label="close"
                            >
                                <CloseIcon fontSize="large" />
                            </IconButton>
                        </DialogTitle>
                        <DialogContent className={classes.dialogColor}>
                            {setChoices ? (
                                <Grid container spacing={1}
                                    direction="row"
                                    justify="center"
                                    alignItems="center">
                                    {setChoices.map((currentItem: any) => (
                                        <Grid item xs={12} sm={6} lg={4} key={currentItem.title}>
                                            <Card className={classes.card}>
                                                <CardActionArea onClick={() => showContentItems(currentItem.id)}>
                                                    <CardMedia
                                                        className={classes.media}
                                                        image={currentItem.url}
                                                    />
                                                </CardActionArea>
                                                <CardActions className={classes.cardActions}>
                                                    <Button
                                                        variant="contained"
                                                        size="large"
                                                        className={classes.button}
                                                        endIcon={<SendIcon />}
                                                        onClick={() => showContentItems(currentItem.id)}
                                                    >
                                                        <Typography variant="h5" color="inherit">
                                                            {intl.get(currentItem.title)}
                                                        </Typography>

                                                    </Button>
                                                </CardActions>
                                            </Card>
                                        </Grid>
                                    ))}
                                </Grid>
                            ) : (
                                    "No Items found"
                                )}
                        </DialogContent>
                        <DialogActions></DialogActions>
                    </Dialog>
                </div>
            ) : null}
        </div>
    );
};

export default DashboardItem;