import * as React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import BrightnessMediumIcon from "@material-ui/icons/BrightnessMedium";
import IconButton from "@material-ui/core/IconButton";
import DrawIcon from "@material-ui/icons/Edit";
import HomeIcon from '@material-ui/icons/HomeSharp';
import LanguageSharpIcon from '@material-ui/icons/LanguageSharp';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from "@material-ui/core/styles";
import DrawCanvas from "./DrawCanvas";
import intl from 'react-intl-universal';
import { Divider } from "@material-ui/core";
import { LOCALE_MR, LOCALE_EN } from './Constants';

const useStyles = makeStyles((theme) => ({
    root: {},
    menuButton: {
        marginRight: theme.spacing(2),
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
    },
    title: {
        flexGrow: 1,
    },
    button: ({
        margin: theme.spacing(0.5)
    }),
}));

const NavBar = (props: any) => {
    const classes = useStyles();

    const [brushColor, setBrushColor] = React.useState("#000000");
    let [open, setOpen] = React.useState(false);

    const handleBurshColor = (brushColor: string) => {
        setBrushColor(brushColor);
        console.log("brushColor : " + brushColor);
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    let setCanvasFlag = () => {
        setOpen(false);
    }

    //reset to dashboard view
    const handleDashboardView = () => {
        props.updateDashboardFlag(true, null, intl.get('APP_NAME'));
    };

    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);

    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        setAnchorEl(event.currentTarget);
    };

    const menuClosed = () =>{
        setAnchorEl(null);
    }

    const getCurrentLocale = () =>{
    return intl.determineLocale({
            urlLocaleKey: 'lang',
            cookieLocaleKey: 'lang'
          });
    };

    const setEnglishLocale = () => {
        //if(getCurrentLocale() !== LOCALE_EN){
            props.handleLocalChange(LOCALE_EN);
        //}
        setAnchorEl(null);
    };

    const setMarathiLocale = () => {
        if(getCurrentLocale() !== LOCALE_MR){
            props.handleLocalChange(LOCALE_MR);
        }
        setAnchorEl(null);
    };

    return (
        <div className={classes.root}>
            <AppBar position="fixed" className={classes.appBar}>
                <Toolbar >
                    <Typography variant="h5" color="inherit" className={classes.title}>
                        {props.navTitle}
                    </Typography>
                    <div>

                    {!props.dashboardFlag && <IconButton
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            color="inherit"
                            edge="end"
                            onClick={handleDashboardView}
                        >
                            <HomeIcon />
                        </IconButton>}

                        <IconButton
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            color="inherit"
                            edge="end"
                            onClick={handleClickOpen}
                        >
                            <DrawIcon />
                        </IconButton>

                        <IconButton
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            color="inherit"
                            edge="end"
                            onClick={handleClick}
                        >
                            <LanguageSharpIcon />
                        </IconButton>
                        <Menu
                            id="simple-menu"
                            anchorEl={anchorEl}
                            keepMounted
                            open={Boolean(anchorEl)}
                            onClose={menuClosed}
                        >
                            <MenuItem onClick={setEnglishLocale}>
                            <Typography variant="h5" color="inherit">
                                    English
                                </Typography>
                            </MenuItem>
                            <Divider />
                            <MenuItem onClick={setMarathiLocale}>
                            <Typography variant="h5" color="inherit">
                                    
                                </Typography>
                            </MenuItem>
                        </Menu>

                        

                        {/* <IconButton
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            color="inherit"
                            edge="end"
                        >
                            <BrightnessMediumIcon />
                        </IconButton> */}
                    </div>
                </Toolbar>
            </AppBar>
            {open && <DrawCanvas brushColor={brushColor} updateBrushColor={handleBurshColor} resetCanvasFlag={setCanvasFlag} />}
        </div>
    );
};

export default NavBar;