import * as React from "react";
import CardActionArea from '@material-ui/core/CardActionArea';
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import InteractiveContent from './InteractiveContent';
import * as mulakshar from './../data/mulakshar.json';
import * as marAnk from './../data/mar-ank.json';
import * as mar1to50 from './../data/mar-1to50.json';
import * as mar1to100 from './../data/mar-1to100.json';
import * as abcd from './../data/abcd.json';
import * as engNumbers from './../data/eng-numbers.json';
import * as eng1to50 from './../data/eng-1to50.json';
import * as eng1to100 from './../data/eng-1to100.json';
import * as mulaksharKana from './../data/mulakshar-kana.json';
import * as mulaksharPahiliWelanti from './../data/mulakshar-pahili-welanti.json';
import * as mulaksharDusriWelanti from './../data/mulakshar-dusri-welanti.json';
import * as mulaksharPahilaUkar from './../data/mulakshar-pahila-ukar.json';
import * as mulaksharDusraUkar from './../data/mulakshar-dusra-ukar.json';
import * as mulaksharEkMatra from './../data/mulakshar-ek-matra.json';
import * as mulaksharDonMatra from './../data/mulakshar-don-matra.json';
import * as mulaksharKanaEkMatra from './../data/mulakshar-ek-kana-ek-matra.json';
import * as mulaksharKanaDonMatra from './../data/mulakshar-ek-kana-don-matra.json';
import * as mulaksharAnuswar from './../data/mulakshar-anuswar.json';
import * as mulaksharArdhChandr from './../data/mulakshar-ardhchandr.json';
import * as mulaksharKanaArdhChandr from './../data/mulakshar-kana-ardhchandr.json';
import * as mulaksharWisarg from './../data/mulakshar-wisarg.json';
import intl from 'react-intl-universal';

const useStyles = makeStyles((theme) => ({
  root: {
    color: theme.palette.primary.contrastText,
    textAlignLast: "center",
    justifyItems: "center",
    borderRadius: 0,
  },
  media: {
    height: 0,
    paddingTop: "56.25%",
    backgroundSize: "cover"
  },
}));

const InteractiveItem = (props: any) => {

  const classes = useStyles(props.item);
  const [open, setOpen] = React.useState(false);
  const [contentData, setContentData] = React.useState({});
  const theme = useTheme();
  const [contentTitle, setContentTitle] = React.useState(String);

  const handleClickOpen = (item: any) => {
    setContentData(getInteractiveContent(item.parentID, item.id));
    setOpen(true);
  };

  const getInteractiveContent = (parentId: number, currentContentId: number) => {
    if (parentId === 1 && currentContentId === 1) {
      setContentTitle(intl.get('MARATHI_MULAKSHARE') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulakshar.data;
    }


    if (parentId === 1 && currentContentId === 2) {
      setContentTitle(intl.get('MARATHI_ANK') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return marAnk.data;
    }


    if (parentId === 1 && currentContentId === 3) {
      setContentTitle(intl.get('MARATHI_ANK_1_TO_50') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mar1to50.data;
    }


    if (parentId === 1 && currentContentId === 4) {
      setContentTitle(intl.get('MARATHI_ANK_1_TO_100') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mar1to100.data;
    }


    if (parentId === 1 && currentContentId === 5) {
      setContentTitle(intl.get('MARATHI_KANA') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharKana.data;
    }


    if (parentId === 1 && currentContentId === 6) {
      setContentTitle(intl.get('MARATHI_PAHILI_WELANTI') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharPahiliWelanti.data;
    }


    if (parentId === 1 && currentContentId === 7) {
      setContentTitle(intl.get('MARATHI_DUSRI_WELANTI') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharDusriWelanti.data;
    }


    if (parentId === 1 && currentContentId === 8) {
      setContentTitle(intl.get('MARATHI_PAHILA_UKAR') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharPahilaUkar.data;
    }


    if (parentId === 1 && currentContentId === 9) {
      setContentTitle(intl.get('MARATHI_DUSRA_UKAR') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharDusraUkar.data;
    }


    if (parentId === 1 && currentContentId === 10) {
      setContentTitle(intl.get('MARATHI_EK_MATRA') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharEkMatra.data;
    }


    if (parentId === 1 && currentContentId === 11) {
      setContentTitle(intl.get('MARATHI_DON_MATRA') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharDonMatra.data;
    }


    if (parentId === 1 && currentContentId === 12) {
      setContentTitle(intl.get('MARATHI_KANA_EK_MATRA') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharKanaEkMatra.data;
    }


    if (parentId === 1 && currentContentId === 13) {
      setContentTitle(intl.get('MARATHI_KANA_DON_MATRA') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharKanaDonMatra.data;
    }


    if (parentId === 1 && currentContentId === 14) {
      setContentTitle(intl.get('MARATHI_ANUSWAR') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharAnuswar.data;
    }


    if (parentId === 1 && currentContentId === 15) {
      setContentTitle(intl.get('MARATHI_ARDHCHANDR') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharArdhChandr.data;
    }


    if (parentId === 1 && currentContentId === 16) {
      setContentTitle(intl.get('MARATHI_KANA_ARDHCHANDR') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharKanaArdhChandr.data;
    }


    if (parentId === 1 && currentContentId === 17) {
      setContentTitle(intl.get('MARATHI_WISARG') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return mulaksharWisarg.data;
    }


    if (parentId === 2 && currentContentId === 1){
      setContentTitle(intl.get('ENGLISH_ALPHABETS') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return abcd.data;
    }

    if (parentId === 2 && currentContentId === 2){
      setContentTitle(intl.get('ENGLISH_NUMBERS') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return engNumbers.data;
    }

    if (parentId === 2 && currentContentId === 3){
      setContentTitle(intl.get('ENGLISH_NUMBERS_1_TO_50') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return eng1to50.data;
    }
      
    if (parentId === 2 && currentContentId === 4){
      setContentTitle(intl.get('ENGLISH_NUMBERS_1_TO_100') + " " + intl.get('LANGUAGE_SPEAK_TITLE'));
      return eng1to100.data;
    }

    return null;
  }

  let resetContentFlag = () => {
    setOpen(false);
  }

  return (
    <div>
      {props.item ? (
        <Card className={classes.root}>
          <CardActionArea onClick={() => handleClickOpen(props.item)}>
            <CardMedia
              className={classes.media}
              image={props.item.url}
            />
          </CardActionArea>
        </Card>
      ) : null}

      {open && <InteractiveContent contentTitle={contentTitle} contentData={contentData} resetContentFlag={resetContentFlag} />}
    </div>
  );
};

export default InteractiveItem;