import * as React from "react";
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import Box from "@material-ui/core/Box";
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Collapse from '@material-ui/core/Collapse';
import PlayCircleOutlineIcon from '@material-ui/icons/PlayCircleOutline';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Hidden from '@material-ui/core/Hidden';
import VideoPlayer from './VideoPlayer';

const drawerWidth = 350;

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        width: drawerWidth,
    },
    drawerContainer: {
        overflow: 'auto',
        backgroundColor: '#f7f8fa'
    },
    content: {
        flexGrow: 1,
        paddingTop: theme.spacing(0),
    },
    nested: {
        paddingLeft: theme.spacing(4),
    },
    heading: {
        fontSize: theme.typography.pxToRem(30),
        fontWeight: "normal",
        flexShrink: 1,
    },
    listItems: {
        fontSize: theme.typography.pxToRem(20),
        fontWeight: "normal",
    },
}));

const Contents = (props: any) => {
    const classes = useStyles();
    const [open, setOpen] = React.useState(0);
    
    // current url will be defaulted to starting index from content list
    const [currentURL, setcurrentURL] = React.useState(props.contentData[0].subCategories[0].url);

    const handleClick = (index: number) => {
      if(open === index)
        setOpen(0);
      else
        setOpen(index);
    };

    const setUrlStream = (streamURL: string) => {
        setcurrentURL(streamURL);
      };

    
    const contentsData = (
        <div className={classes.drawerContainer}>
            <List>
            {props.contentData ? (
                <div>
                {props.contentData.map((currentItem, index) => (
                    <div >
                        <ListItem key={index} button onClick={() => {handleClick(index)}}>
                            <ListItemText>
                                <Typography className={classes.heading}>{currentItem.title}</Typography>
                            </ListItemText>
                            {index === open ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={index === open} timeout="auto" unmountOnExit>
                            <List component="div" disablePadding >
                                {currentItem.subCategories.map((subCategories, index) => (
                                    <ListItem button className={classes.nested} key={index} onClick={() => {setUrlStream(subCategories.url)}}>
                                      <ListItemIcon><PlayCircleOutlineIcon /></ListItemIcon>
                                        <ListItemText>
                                            <Typography className={classes.listItems}>{subCategories.subItemTitle}</Typography>    
                                        </ListItemText>
                                    </ListItem>
                                ))}
                            </List>
                        </Collapse>
                        <Divider />
                    </div>
                ))}
                </div>
                ) : (
                    <Typography variant="caption" display="block" gutterBottom>
                        Coming soon
                    </Typography>
                )}
            </List>
            <Typography variant="caption" display="block" gutterBottom>
                Copyright  2020 Unnat Pravah
            </Typography>
        </div>
    );

    return (
        <div className={classes.root}>
            <CssBaseline />
            <Hidden xsDown implementation="css">
                <Drawer
                    className={classes.drawer}
                    variant="permanent"
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                >
                    <Toolbar />
                    {contentsData}
                </Drawer>
            </Hidden>
            <main className={classes.content}>
                <Box
                    justifyContent="space-around"
                    style={{ height: "35vh"}}
                >
                    <VideoPlayer url={currentURL} />
                </Box>
                <Hidden smUp implementation="css">
                    <Box style={{ height: "55vh", overflow: "auto" }}>
                      {contentsData}
                    </Box>
                </Hidden>
            </main>
        </div>
    );
};

export default Contents;