import * as React from "react";
import NavBar from "./components/NavBar";
import Dashboard from "./components/Dashboard";
import Contents from "./components/Contents"
import { Toolbar } from "@material-ui/core";
import intl from 'react-intl-universal';
import { LOCALE_MR, LOCALE_EN } from './components/Constants';
import InteractiveDashboard from "./components/InteractiveDashboard";

// app locale data
const locales =
{
    "en-US": require("./locales/en-US.json"),
    "mr-IN": require("./locales/mr-IN.json")
};

class App extends React.Component {

    // Set state values
    state = {
        navTitle: "",
        dashboard: true,
        isContentIndex: false,
        contentData: [],
        localInitDone: false,
        interactive: false,
    };


    async componentDidMount() {
        await this.loadLocales();
        if (this.state.localInitDone) {
            this.setState({ navTitle: intl.get('APP_NAME') });
        }
    }

    loadLocales() {
        let currentLocale = intl.determineLocale({
            urlLocaleKey: 'lang',
            cookieLocaleKey: 'lang'
        });

        if (currentLocale !== LOCALE_EN && currentLocale !== LOCALE_MR)
            currentLocale = LOCALE_MR;

        //initially locale is set to marathi
        if (window.location.search === '') {
            currentLocale = LOCALE_MR;
        }

        // init method will load CLDR locale data according to currentLocale
        intl.init({
            currentLocale: currentLocale,
            locales,
        })
            .then(() => {
                this.setState({ localInitDone: true });
            });
    }

    render() {

        return (
            this.state.localInitDone &&
            <div>
                <NavBar navTitle={this.state.navTitle}
                    dashboardFlag={this.state.dashboard}
                    updateDashboardFlag={this.updateDashboardFlag}
                    handleLocalChange={this.handleLocalChange} />
                <Toolbar />
                {this.state.interactive && <InteractiveDashboard updateDashboardFlag={this.updateDashboardFlag} contentData={this.state.contentData} />}
                {this.state.dashboard && <Dashboard updateDashboardFlag={this.updateDashboardFlag} />}
                {this.state.isContentIndex && <Contents contentData={this.state.contentData} />}
            </div>
        );
    }

    updateDashboardFlag = (isAvailable: boolean, contentData, navTitle: string) => {
        if ((navTitle === intl.get('LANGUAGE_MARATHI') + " " + intl.get('LANGUAGE_SPEAK_TITLE')) ||
            (navTitle === intl.get('LANGUAGE_ENGLISH') + " " + intl.get('LANGUAGE_SPEAK_TITLE')) ||
            (navTitle === intl.get('LANGUAGE_MATHS') + " " + intl.get('LANGUAGE_SPEAK_TITLE'))) {
            this.setState({
                dashboard: false,
                isContentIndex: false,
                interactive: isAvailable,
                contentData: contentData,
                navTitle: navTitle
            });
        } else {
            this.setState({
                dashboard: isAvailable,
                isContentIndex: !isAvailable,
                interactive: false,
                contentData: contentData,
                navTitle: navTitle
            });
        }
    }

    handleLocalChange = (selectedLocale: string) => {
        location.search = `?lang=${selectedLocale}`;
    };
}

export default App;