import * as React from "react";
import Grid from "@material-ui/core/Grid";
import DashboardItem from "./DashboardItem";
import { dashboardChoices } from "./Constants";

class Dashboard extends React.Component {
    state = {
    };

     handleDashboardView = (dashboardFlag: boolean, contentData, navTitle: string) => {
         this.props.updateDashboardFlag(dashboardFlag, contentData, navTitle);
    }

    render() {
        return (
            <div>
                {dashboardChoices ? (
                    <Grid
                        container
                        spacing={5}
                        style={{minHeight: "90vh" }}
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                        {dashboardChoices.map((currentItem) => (
                            <Grid item xs={12} sm={6} lg={4} xl={3} key={currentItem.title}>
                                <DashboardItem item={currentItem} handleDashboardView={this.handleDashboardView} />
                            </Grid>
                        ))}
                    </Grid>
                ) : (
                        "No Items found"
                    )}
            </div>
        );
    }
}

export default Dashboard;
