import * as React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import CloseIcon from "@material-ui/icons/HighlightOffSharp";
import Slide from "@material-ui/core/Slide";
import CanvasDraw from "react-canvas-draw";
import BorderColorIcon from '@material-ui/icons/CreateSharp';
import DeleteSharpIcon from '@material-ui/icons/DeleteSharp';
import intl from 'react-intl-universal';

const useStyles = makeStyles((theme) => ({
    root: {
        "& > *": {
            color: "#30c6cc",
            position: "fixed",
            bottom: "5px",
            right: "5px",
            overflow: "hidden",
        },
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    title: {
        flex: 1,
    },
    centerControls: {
        flexGrow: 1,
        align: 'center',
    },
    blackColor: {
        color: 'black',
    },
    blueColor: {
        color: 'blue',
    },
    redColor: {
        color: 'red',
        
    },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


const DrawCanvas = (props: any) => {
    const classes = useStyles();
    const [open, setOpen] = React.useState(true);

    const handleClose = () => {
        setOpen(false);
        props.resetCanvasFlag();
        props.updateBrushColor("#000000");
    };

    // we create ref on component instance
    let saveableCanvas = React.useRef();

    return (
        <div>
            <Dialog
                fullScreen
                scroll="paper"
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar>
                    <Toolbar>
                        <Typography variant="h5" className={classes.title}>
                            {intl.get('WRITING_BOARD')}
                        </Typography>
                        <div className={classes.centerControls}>
                            <IconButton
                                edge="end"
                                color="inherit"
                                aria-label="close"
                                onClick={() => {
                                    props.updateBrushColor("#000000");
                                }}
                            >
                                <BorderColorIcon fontSize="large" className={classes.blackColor} />
                            </IconButton>

                            <IconButton
                                edge="end"
                                color="inherit"
                                aria-label="close"
                                onClick={() => {
                                    props.updateBrushColor("#0000ff");
                                }}
                            >
                                <BorderColorIcon fontSize="large" className={classes.blueColor} />
                            </IconButton>

                            <IconButton
                                edge="end"
                                color="inherit"
                                aria-label="close"
                                onClick={() => {
                                    props.updateBrushColor("#ff0000");
                                }}
                            >
                                <BorderColorIcon fontSize="large" className={classes.redColor} />
                            </IconButton>

                            <IconButton
                                edge="end"
                                color="inherit"
                                onClick={() => {
                                    saveableCanvas.current.clear();
                                }}
                                aria-label="close"
                            >
                                <DeleteSharpIcon fontSize="large" />
                            </IconButton>
                        </div>
                        <IconButton
                            edge="start"
                            color="inherit"
                            onClick={handleClose}
                            aria-label="close"
                        >
                            <CloseIcon fontSize="large" />
                        </IconButton>
                    </Toolbar>
                </AppBar>
                <div>
                    <CanvasDraw
                        ref={saveableCanvas}
                        lazyRadius={0}
                        brushRadius={3}
                        brushColor={props.brushColor}
                        catenaryColor={props.brushColor}
                        canvasWidth={window.innerWidth}
                        canvasHeight={window.innerHeight} />
                </div>
            </Dialog>
        </div>
    );
};

export default DrawCanvas;